body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@@font-face {
  font-family: 'Avenir Next';
  font-style: normal;
  src: url(/static/media/Avenir-Medium-09.0fe148f8.ttf); /* IE9 Compat Modes */
}

.App {
  text-align: center;
  color: white;
  font-family: 'Avenir Next'
}

.App a{
  color: #bfbfbf;
}

h2{
  font-size: calc(28px + 2vmin);
  padding: 0px 20px;
  background: linear-gradient(to top, #d5dee7 0%, #ffafbd 0%, #c9ffbf 100%);
  -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;}

p{
  margin-top: 14px
}

.Clock {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px 40px;
  margin-top: 26px;
  margin-bottom: 30px
}

.column{
  display: flex;
  flex-direction: column;
  padding-right: 20px;
  padding-bottom: 20px
}

.figure{
  font-size: 50px;
  font-weight: 600
}

@media only screen and (max-width: 800px) {
  .Clock{
    margin-top: 10px
  }

}

